function MessHesap(){
	  const [Userim, setUser] = useState('');
	    const [PAWIM, setPAW] = useState('');
	    const [tik, setTik] = useState(false);
	    const [durum, setDurum] = useState('');
	 const handleUsernameChange = (event) => {
    setUser(event.target.value);
  };
  
   const handlePassChange = (event) => {
    setPAW(event.target.value);
  };
	
	
const hesapekle = () => {
  setTik(true);
  setDurum('Giriş yapılıyor');
  const url = `http://localhost:3004/start-messenger?username=${Userim}&paw=${PAWIM}`;

  fetch(url)
    .then(response => {
      if (!response.ok) {
        throw new Error('HTTP isteği başarısız oldu');
      }
      // İşlemler yapmak isterseniz burada yapabilirsiniz
    })
    .catch(error => {
      console.error('Hata:', error.message);
    });
}
	
	return (
	<div className="col-8">
	  <div className="card canChange">
                <div className="card-body">
                  <h4 className="card-title">Messenger Hesabı Ekleyin</h4>
                  <p className="card-description">
                    Kullanıcı adı ve şifre yazınız ve biraz bekleyiniz (Facebook Bilgileriniz.)
                  </p>
                  <div className="form-group row">
                    <div className="col">
                      <label>Username</label>
                      <div id="the-basics">
                        <input onChange={handleUsernameChange} value={Userim} className="typeahead" type="text" placeholder="leomessi" />
                      </div>
                    </div>
                    <div className="col">
                      <label>Şifre</label>
                      <div id="bloodhound">
                        <input onChange={handlePassChange} value={PAWIM} className="typeahead" type="text" placeholder="BenimBabamRonaldo" />
                      </div>
                    </div>
					
                  </div>
				  {tik && (

					<p>{durum}</p>
					)}
					<p>Eğer kullanıcı adı ve şifre otomatik doldurulmaz ise kendiniz doldurabilirsiniz.</p>
				  <button onClick={hesapekle} className="btn btn-info">Giriş yap</button>
                </div>
              </div>
	
	</div>
	
	);
	
	
	
}




