function GetUserInfo() {
  const username = localStorage.getItem('username');
  const [userInfo, setUserInfo] = useState(null);
  const [loading, setLoading] = useState(true); // Veri yüklenirken göstermek için bir yükleniyor durumu ekleyin

  useEffect(() => {
    if (username) {
      fetch(`https://api.suphi.org/tokencheck.php?token=${username}`)
        .then((response) => response.json())
        .then((data) => {
          if (data.Sonuc !== 'Tamamdır') {
            localStorage.removeItem('username');
            setUserInfo(null); // Kullanıcıyı bulamadığınızda userInfo'yi temizleyin
          } else {
            setUserInfo(data);
          }
          setLoading(false); // Veri yükleme tamamlandığında yükleniyor durumunu false yapın
        })
        .catch((error) => {
          console.error('API isteği başarısız:', error);
          setLoading(false); // Hata durumunda da yükleniyor durumunu false yapın
        });
    } else {
      setUserInfo(null); // Kullanıcı adı yoksa userInfo'yi temizleyin
      setLoading(false); // Kullanıcı adı yoksa yükleniyor durumunu false yapın
    }
  }, [username]);

  if (loading) {
    // Veri yüklenirken yükleniyor durumunu gösterin
    return <p>Yükleniyor...</p>;
  }

  if (!userInfo) {
    return <Loginsayfax />;
  }

  return userInfo;
}


