function Hizmetler() {
    const [veriler, setVeriler] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [loadingTimeMessage, setLoadingTimeMessage] = useState(null);
    const [neyigoster, setNeyigoster] = useState('');

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://api.suphi.org/rich/Hizmetler.php');
                if (response.ok) {
                    const data = await response.json();
                    setVeriler(data.Veriler);
                    setNeyigoster(data.Neyigoster);
                    setIsLoading(false);
                }
            } catch (error) {
                console.error('API verileri çekerken hata oluştu:', error);
                setLoadingTimeMessage('API verileri alınamadı. http://www.suphi.org kontrol eder misiniz?');
            }
        };

        fetchData();

        const fiveSecondsTimeout = setTimeout(() => {
            if (isLoading) {
                setLoadingTimeMessage('Biraz daha bekleyin...');
            }
        }, 5000);

        const tenSecondsTimeout = setTimeout(() => {
            if (isLoading) {
                setLoadingTimeMessage('http://www.suphi.org kontrol eder misiniz?');
            }
        }, 10000);

        return () => {
            clearTimeout(fiveSecondsTimeout);
            clearTimeout(tenSecondsTimeout);
        };
    }, [isLoading]);
  
    return (
        <div className="col-md-12 grid-margin stretch-card">
            <div className="card">
                <div className="card-body">
                    <p className="card-title mb-0">{neyigoster}</p>
                    <div className="table-responsive">
                        {isLoading ? (
                            <p>{loadingTimeMessage || 'Loading...'}</p>
                        ) : (
                            <div>
                                <table className="table table-striped table-borderless">
                                    <thead>
                                        <tr>
                                            {veriler.length > 0 && Object.keys(veriler[0]).map((key) => (
                                                <th key={key}>{key}</th>
                                            ))}
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {veriler.map((veri, index) => (
                                            <tr key={index}>
                                                {Object.keys(veri).map((key) => (
                                                    <td key={key}>
                                                        {key === "Rütbe" ? (
                                                            parse(veri[key])
                                                        ) : (
                                                            veri[key]
                                                        )}
                                                    </td>
                                                ))}
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
}

function GetSecure() {
	const username = localStorage.getItem('username');

  // Sonucu saklayacak bir state tanımla
  const [sonuc, setSonuc] = useState(null);

  // Kullanıcı adı mevcutsa API'ye istek gönder
  useEffect(() => {
    if (username) {
      fetch(`https://api.suphi.org/tokencheck.php?token=${username}`)
        .then((response) => response.json())
        .then((data) => {
          // API yanıtını sonuç state'ine kaydet
          setSonuc(data.Sonuc);
		  console.log(data.Sonuc);
		  if(data.Sonuc != 'Tamamdır'){
			    console.log(username);
			  localStorage.removeItem('username');
			  
			 return <Loginsayfax />; 
		  }
		
        })
        .catch((error) => {
          console.error('API isteği başarısız:', error);
        });
    }
  }, [username]);

  // Kullanıcı adı yoksa giriş sayfasını göster
  if (!username) {
    return <Loginsayfax />;
  }
}

